import { EComponent } from "../framework/Component";
import { ComponentType } from "../framework/Config";

export class TransformComponent extends EComponent {
    type: ComponentType = ComponentType.TransformComp;
    x: number = 0;
    y: number = 0;
    z: number = 0;
    angle: number = 0;
    moveSpeed: number = 0;
    rotateSpeed: number = 0;
    isMove: boolean = false;
    isRotate: boolean = false;
}



